<template>
  <div>
    <div class="index-content">
      <!-- 页面内容保留，不会二次触发钩子 -->
      <keep-alive>
        <!-- 二级路由 -->
        <router-view></router-view>
      </keep-alive>
    </div>

    <div class="indexNav">
      <van-tabbar v-model="active" active-color="#ff3f7e">
        <van-tabbar-item to="home" name="home" icon="wap-home">首页</van-tabbar-item>
        <van-tabbar-item to="live" name="live" icon="live">电影</van-tabbar-item>
        <van-tabbar-item to="chat" name="chat" icon="chat">论坛</van-tabbar-item>
        <van-tabbar-item to="shopping" name="shopping" icon="cart">购物车</van-tabbar-item>
        <van-tabbar-item to="manage" name="manage" icon="manager">我的</van-tabbar-item>
      </van-tabbar>

    </div>

  </div>
</template>

<script>

  export default {
    name: 'index',

    data() {
      return {
        active: 'home',
      };
    },


    activated(){
      let m = this.$route.matched;
      console.log(m)
      if(m.length == 1){
        this.$router.push({name: 'home'});
        this.active = 'home';
      }else{
        this.active = m[1].name;
      }

    }

  }

</script>

<style scoped lang="less">
  .index-content{
    height: calc(~"100% - 1.333rem");
  }
</style>
